.main-nav {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: 0;
  color: #ffffff;
  width: 100%;
}
@media (min-width: 960px) {
  .main-nav {
    flex-direction: row;
  }
}
.main-nav__logo {
  display: inline-block;
  vertical-align: top;
  margin: 22px 20px;
  width: 75px;
  height: 22px;
}
@media (min-width: 660px) and (max-width: 859px) {
  .main-nav__logo {
    margin-top: 27px;
    margin-bottom: 43px;
    width: 147px;
    height: 40px;
  }
  .main-nav__logo:hover svg path,
  .main-nav__logo:focus svg path {
    fill: #c61352;
  }
  .main-nav__logo:active svg path {
    fill: rgba(255, 255, 255, 0.3);
  }
}
@media (min-width: 960px) {
  .main-nav__logo {
    margin-left: 0;
  }
}
.main-nav__svg--mobile {
  display: block;
}
.main-nav__svg--tablet {
  display: none;
}
.main-nav__svg--desktop {
  display: none;
}
@media (min-width: 660px) {
  .main-nav__svg--mobile {
    display: none;
  }
  .main-nav__svg--tablet {
    display: block;
  }
  .main-nav__svg--desktop {
    display: none;
  }
}
@media (min-width: 960px) {
  .main-nav__svg--mobile {
    display: none;
  }
  .main-nav__svg--tablet {
    display: none;
  }
  .main-nav__svg--desktop {
    display: block;
  }
}
.main-nav__list {
  display: block;
  text-align: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 960px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    border-top: none;
    justify-content: flex-end;
    margin-left: auto;
    width: 760px;
    padding-top: 34px;
    padding-bottom: 36px;
    flex-shrink: 1;
  }
}
.main-nav__list--opened {
  margin-top: 4px;
}
.main-nav__list--closed {
  display: none;
}
@media (min-width: 960px) {
  .main-nav__list--closed {
    display: flex;
  }
}
.main-nav__logo-img--mob {
  display: block;
}
@media (min-width: 660px) {
  .main-nav__logo-img--mob {
    display: none;
  }
}
@media (min-width: 960px) {
  .main-nav__logo-img--mob {
    display: none;
  }
}
.main-nav__logo-img--tab {
  display: none;
}
@media (min-width: 660px) {
  .main-nav__logo-img--tab {
    display: block;
  }
}
@media (min-width: 960px) {
  .main-nav__logo-img--tab {
    display: none;
  }
}
.main-nav__logo-img--des {
  display: none;
}
@media (min-width: 660px) {
  .main-nav__logo-img--des {
    display: none;
  }
}
@media (min-width: 960px) {
  .main-nav__logo-img--des {
    display: block;
  }
}
.main-nav__link {
  display: block;
  margin: 0;
  background-color: #283645;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  padding: 16px 20px;
  box-shadow: 0 -1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
}
.main-nav__link:hover,
.main-nav__link:focus {
  color: #c61352;
}
.main-nav__link:active {
  color: rgba(255, 255, 255, 0.3);
}
.main-nav__toggle {
  position: absolute;
  top: 21px;
  right: 19px;
  width: 50px;
  height: 24px;
  padding: 0;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  outline: 0;
}
.main-nav__toggle:hover svg path {
  fill: #c61352;
}
.main-nav__toggle:active svg path {
  fill: rgba(198, 19, 82, 0.3);
}
.main-nav__toggle--closed svg:first-child {
  display: none;
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 44px;
  }
}
@media (min-width: 960px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 960px) {
  .main-nav__item {
    margin-left: 80px;
  }
  .main-nav__item:first-child {
    margin-left: 0;
  }
  .main-nav__item--active {
    position: relative;
  }
  .main-nav__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -2px;
    left: 0;
    height: 2px;
    background: #ffffff;
  }
}
.main-nav__item a {
  display: block;
  margin: 0;
  background-color: #283645;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  padding: 16px 20px;
  box-shadow: 0 -1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
}
@media (min-width: 960px) {
  .main-nav__item a {
    background-color: transparent;
    border: none;
    box-shadow: none;
    text-transform: none;
    line-height: 18px;
    font-weight: 400;
    padding: 11px 0;
  }
}
.main-nav__item a[href]:hover,
.main-nav__item a[href]:focus {
  color: #c61352;
}
.main-nav__item a[href]:active {
  color: rgba(255, 255, 255, 0.3);
}
.main-nav__item:last-child a {
  box-shadow: 0 -1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15), inset 0 -1px 0 #000000;
}
@media (min-width: 960px) {
  .main-nav__item:last-child a {
    box-shadow: none;
  }
}
@media (max-width: 859px) {
  .main-nav--nojs {
    flex-direction: column;
  }
  .main-nav__list--closed {
    display: none;
  }
  .main-nav__toggle--nojs {
    display: none;
  }
  .main-nav__list--nojs {
    display: block;
  }
}
.downloads {
  padding-top: 28px;
  padding-bottom: 29px;
  text-align: center;
  color: #ffffff;
  background-color: #1d2631;
}
@media (min-width: 660px) {
  .downloads {
    padding: 93px 0;
    width: 300px;
    position: relative;
    background-color: transparent;
    z-index: 2;
  }
}
@media (min-width: 960px) {
  .downloads {
    padding: 109px 0;
  }
}
@media (max-width: 659px) {
  .downloads__wrapper {
    width: 320px;
    padding-left: 20px;
    padding-right: 20px;
    margin: 0 auto;
  }
}
.downloads__btn {
  width: 100%;
  margin-bottom: 32px;
}
@media (min-width: 660px) {
  .downloads__btn {
    margin-bottom: 46px;
  }
}
.downloads__types {
  font-size: 0;
}
.downloads__type {
  display: inline-block;
  vertical-align: top;
  width: 47px;
  height: 56px;
  background-color: #c61352;
  margin-right: 16px;
  margin-left: 16px;
}
.downloads__type--iphone {
  background: url("../img/logo-apple.svg") no-repeat top left;
  background-size: 47px 56px;
}
.downloads__type--android {
  background: url("../img/logo-android.svg") no-repeat center 8px;
  background-size: 43px 47px;
}
.downloads__type--windows {
  background: url("../img/logo-microsoft.svg") no-repeat right 12px;
  background-size: 44px 41px;
}
.downloads__info {
  font-weight: 300;
  margin-top: 22px;
}
@media (min-width: 660px) {
  .downloads__info {
    font-size: 18px;
    line-height: 24px;
    margin-top: 38px;
  }
}
@media (min-width: 960px) {
  .downloads__info {
    margin-top: 55px;
  }
}
@media (max-width: 659px) {
  .downloads__title {
    position: absolute;
    overflow: hidden;
    clip: rect(0 0 0 0);
    height: 1px;
    width: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
  }
}
@media (min-width: 660px) {
  .downloads__title {
    padding-left: 55px;
    padding-right: 55px;
    margin-top: 0;
    margin-bottom: 28px;
    font-size: 40px;
    line-height: 48px;
    font-weight: 300;
  }
}
@media (min-width: 960px) {
  .downloads__title {
    padding-left: 30px;
    padding-right: 30px;
    margin-bottom: 51px;
    font-size: 50px;
    line-height: 60px;
  }
}
.btn {
  display: inline-block;
  vertical-align: top;
  padding: 15px 30px 16px;
  border: none;
  font-family: "open sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 18px;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  outline: none;
  cursor: pointer;
}
.btn--upload {
  border: 3px solid #c61352;
  border-radius: 30px;
  color: #c61352;
  background-color: transparent;
}
.btn--upload:hover,
.btn--upload:focus {
  color: #ffffff;
  background-color: #c61352;
}
.btn--upload:active {
  color: rgba(255, 255, 255, 0.3);
}
.btn--cancel {
  border: 3px solid #283645;
  border-radius: 30px;
  color: #283645;
  background-color: transparent;
  opacity: 0.1;
}
.btn--cancel:hover,
.btn--cancel:focus {
  opacity: 0.3;
}
.btn--cancel:active {
  background-color: #283645;
  color: #ffffff;
  opacity: 0.3;
}
.btn--submit {
  padding: 17px 30px 16px;
  border-radius: 3px;
  font-size: 18px;
  line-height: 30px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #2aab6d;
}
.btn--download {
  border: 3px solid #ffffff;
  border-radius: 30px;
  color: #ffffff;
  background-color: transparent;
}
.btn--download:hover,
.btn--download:focus {
  background-color: #ffffff;
  color: #c61352;
}
.btn--download:active {
  color: rgba(198, 19, 82, 0.3);
}
