.plan__wrapper {
  position: relative;
  background-color: @grey;
  padding-top: 91px;
  padding-bottom: 42px;
  .slicer;
  max-width: 100%;
  overflow: hidden;

  @media (min-width: @tablets) {
    .slicer(@height: 50px);
    padding-top: 88px;
    padding-bottom: 68px;
  }

  @media (min-width: @desktops) {
    .slicer(@height: 65px);
    padding-top: 140px;
    padding-bottom: 96px;
  }
}

.plan__slide {
  @media (min-width: @tablets) {
    .container;
  }
}

.plan__table {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  border-collapse: collapse;
  width: 262.5%;

  @media (min-width: @tablets) {
    width: 100%;
  }
}

.plan__features-header {
  @media (max-width: @phones-max) {
    display: none;
  }
}

.plan__header + .plan__header {
  border-left: 2px solid transparent;
  border-right: 2px solid transparent;
}

.plan__header {
  position: relative;
  background-color: @main-color;
  color: @white;
  text-transform: uppercase;
  padding: 20px;
  padding-top: 17px;
  background-clip: padding-box;

  @media (min-width: @tablets) {
    padding-top: 10px;
    padding-bottom: 9px;
    width: 25%;
  }

  @media (min-width: @desktops) {
    padding: 20px;
    padding-top: 17px;
  }

  &:first-of-type {
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }

  &:last-of-type {
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    border-left: none;
    border-right: none;
  }

  &--flag::before {
    content: "";
    position: absolute;
    top: 0;
    left: -2px;
    width: 74px;
    height: 73px;
    background: url("../img/bg-hit.svg") no-repeat top left;
    background-size: 74px 73px;

    @media (min-width: @tablets) and (max-width: @tablets-max) {
      width: 61px;
      height: 61px;
      background: url("../img/bg-hit-tablet.svg") no-repeat top left;
      background-size: 61px 61px;
    }

    @media (min-width: @tablets) {
      top: 0;
      left: 0;
    }
  }
}

.plan__flag {
  position: absolute;
  top: 6px;
  left: 1px;
  width: 78px;
  height: 73px;
  transform: rotate(-45deg);
  font-weight: 400;

  @media (min-width: @tablets) {
    top: 1px;
    left: 2px;
    width: 61px;
    height: 61px;
  }

  @media (min-width: @desktops) {
    top: 6px;
    left: 3px;
    width: 78px;
    height: 73px;
  }
}

.plan__item {
  position: relative;
  padding-top: 17px;
  padding-right: 68px;
  padding-bottom: 18px;
  padding-left: 22px;
  background-color: @white;
  border: 2px solid @grey-border;
  border-top-width: 0;
  background-clip: padding-box;

  @media (min-width: @tablets) {
    padding: 7px 16px 8px;
  }

  @media (min-width: @desktops) {
    padding: 16px 22px 13px;
  }

  &--feature-title {
    border-top-width: 2px;

    @media (max-width: @phones-max) {
      display: none;
    }
  }
}

.plan__feature {
  @media (min-width: @desktops) {
    font-size: 18px;
  }

  &--name {
    text-transform: uppercase;

    @media (min-width: @tablets) {
      display: none;
    }
  }
}

.plan__name {
  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 1px;

  @media (min-width: @tablets) and (max-width: @tablets-max) {
    margin-bottom: 0;
    font-size: 16px;
  }
}

.plan__price {
  font-size: 24px;
  line-height: 30px;
  font-weight: 300;

  @media (min-width: @tablets) and (max-width: @tablets-max) {
    font-size: 18px;
  }
}

.plan__feature-status {
  position: absolute;
  display: block;
  width: 31px;
  height: 30px;
  top: 13px;
  right: 17px;
  text-indent: -1000px;
  overflow: hidden;

  @media (min-width: @tablets) {
    position: relative;
    top: auto;
    right: auto;
    margin-left: auto;
    margin-right: auto;
  }

  &--on {
    background: url("../img/icon-yes.svg") no-repeat center;
    background-size: 31px 21px;
  }

  &--off {
    background: url("../img/icon-no.svg") no-repeat center;
    background-size: 27px 26px;
  }
}

.plan__switch {
  margin-top: 28px;

  @media (min-width: @tablets) {
    display: none;
  }
}
